<template>
  <v-app class="page">
    <!--Оверлей с выпускником-->
    <div class="overlay-graduate-card-background full-size" v-show="showCard">
      <v-card class="card">
        <v-card-title>
          <v-btn icon elevation="0" @click = "showCard = false" style="right: 4px;position: absolute; top: 6px">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <OverlayGraduateMap
            :image = "this.objects[chosenGraduateId].img"
            :name = "this.objects[chosenGraduateId].name"
            :secondname = "this.objects[chosenGraduateId].secondname"
            :sername = "this.objects[chosenGraduateId].sername"
            :city= "this.objects[chosenGraduateId].workcity"
            :country = "this.objects[chosenGraduateId].workCountry"
            :year = "this.objects[chosenGraduateId].year"
            :university = "this.objects[chosenGraduateId].university"
            :company = "this.objects[chosenGraduateId].company"
            :job = "this.objects[chosenGraduateId].job"
            :contacts = "this.objects[chosenGraduateId].contacts"

        ></OverlayGraduateMap>

      </v-card>
    </div>


    <!--Хеддер-->
    <table class="headder">
      <tr>
        <!--Лого-->
        <td class="headders-cols">
          <router-link :to="{name: 'map'}"><img height="64px" :src="require('/src/assets/maplogo.svg')"
                                                alt="КнАГУ Карта выпускников"></router-link>
        </td>
        <!--Элементы управления-->
        <td class="headders-cols buttons-col">
          <help-button></help-button>
          <login-button></login-button>
        </td>
      </tr>
    </table>
    <!--Фильтры-->
    <v-expansion-panels>
      <v-expansion-panel>
        <v-expansion-panel-header color="#1976D2">
          <h4 style="color: #FFFFFF">Фильтры</h4>
          <template v-slot:actions>
            <v-icon color="#FFFFFF">
              mdi-cog-outline
            </v-icon>
          </template>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <table>
            <v-row>
              <v-col>
                <v-autocomplete label="Год выпуска"></v-autocomplete>
              </v-col>
              <v-col>
                <v-autocomplete label="Ученая степень"></v-autocomplete>
              </v-col>
              <v-col>
                <v-autocomplete label="Год выпуска"></v-autocomplete>
              </v-col>
              <v-col>
                <v-autocomplete label="Ученая степень"></v-autocomplete>
              </v-col>
            </v-row>
          </table>
          <v-btn>Найти</v-btn>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <!--Карта-->
    <div id="map" class="full-size">

      <table class="over-map-controls">
        <tr>
          <td>
            <v-tooltip bottom>
              <template v-slot:activator="{ on }">
                <v-btn @click="enableHeatMap" class="buttons" v-on="on" elevation="0" color="#1976D2" small>
                  <v-icon :color="buttonIconColor()">mdi-fire</v-icon>
                </v-btn>
              </template>
              <span>Тепловая карта</span>
            </v-tooltip>
          </td>
        </tr>
      </table>
    </div>
  </v-app>
</template>

<script>
/*Подключенные элементы*/
import {loadYmap} from 'vue-yandex-maps'
import helpButton from "@/components/HelpButton";
import LoginButton from "@/components/LoginButton";
import jQuery from "jquery";
import OverlayGraduateMap from '@/components/pages/OverlayGraduateMap'

export default {
  name: "MapPage",
  components: {
    LoginButton,
    helpButton,
    OverlayGraduateMap
  },
  data: () => ({
    showCard: false,
    //Карта
    map: null,
    chosenGraduateId: 0,
    placemarks: null,
    //Тепловая карта
    heatMapActive: true,
    heatMap: null,
    /*Центр карты при загрузке страницы*/
    mapCenterCoords: [40, 80],
    /*Начальное приближение карты*/
    mapZoom: 4,
    /*Элементы управления на карте*/
    mapControls: ['zoomControl'],
    mapCluserOptions: {},
    objects: [
      {
        coordx: 54,
        coordy: 66,
        workCountry: 'Россия',
        workcity: 'Москва',
        name: "Анна",
        sername: "Бах",
        secondname:"Николаевна",
        job: "Middle-программист",
        img: "no-photo-f.jpg",
        year: '2022',
        university: 'КнАГУ',
        company: 'Яндекс',
        contacts: ['E-mail: keepsecret@mail.ru', 'Телефон: 89144444444', 'Телефон: 89144444444', 'Телефон: 89144444444']
      },
      {
        coordx: 54,
        coordy: 66,
        workCountry: 'Россия',
        workcity: 'Питер',
        name: "Антонио",
        sername: "Донжуани",
        secondname:"",
        job: "Middle-программист",
        img: "no-photo-m.jpg",
        year: '2022',
        university: 'КнАГУ',
        company: 'Evil-net',
        contacts: ['E-mail: keepsecret@mail.ru', 'Телефон: 89144444444',]
      },

    ],
  }),
  methods: {

    /**
     * Возвращает новый цвет кнопки при активации/деактивации тепловой карты
     * @returns {string}
     */
    buttonIconColor() {
      if (!this.heatMapActive) {
        return '#56db40';
      }
      return '#E7F3FE';
    },
    /**
     * Кастомный лейаут для балуна кластеризованной метки
     * @returns {string}
     */
    customContentLayoutForCluster() {
      return '<div class="cluster-content">' + this.customContentLayout() + '</div>';
    },
    /**
     * Кастомный лейаут
     * @returns {string}
     */
    customContentLayout() {
      return '<button data-placemarkid="{{ geoObject.properties.placemarkId }}" class="list_item"><table class = "full-size" style="table-layout: fixed"> ' +
          '<thead> ' +
          '<tbody> ' +
          '<tr> ' +
          '<td class= "headder-table-row">{{ properties.balloonContentHeader|raw }}</td> ' +
          '<td><div class="ballon_body">{{ properties.balloonContentBody|raw }}</div><div class=ballon_footer>{{ properties.balloonContentFooter|raw }}</div></td> ' +
          '</tr> ' +
          '</tbody> ' +
          '</table></button>';
    },
    /**
     * Кастомный лейаут для балуна не кластеризованной метки
     * @returns {string}
     */
    customContentLayoutForSolo() {
      return '<div class="solo-card-content">' + this.customContentLayoutForCluster() + '</div>';
    },
    /**
     * Показывает все гео-метки на карте
     */
    showPlacemarks() {
      this.setPlaseMarks(this.map, []);
    },
    /**
     * Скрывает все гео-метки на карте
     */
    hidePlacemarks() {
      this.setPlaseMarks(this.map, this.objects);
    },
    /**
     * Установка новых меток на карту, путем удаления старых
     * @param map
     * @param objects
     */
    setPlaseMarks(map, objects) {
      if (map) {
        /*Удаляем все старые гео объекты*/
        map.geoObjects.removeAll();
        //Лейауты для балунов одного объекта и кластеризованных объектов
        let customItemContentLayout = window.ymaps.templateLayoutFactory.createClass(this.customContentLayoutForCluster());
        let soloCustomContentLayout = window.ymaps.templateLayoutFactory.createClass(this.customContentLayoutForSolo());
        //Настройки кластеризации
        let clusterer = new window.ymaps.Clusterer({
          clusterDisableClickZoom: true,
          clusterOpenBalloonOnClick: true,
          // Устанавливаем стандартный макет балуна кластера "Карусель".
          clusterBalloonContentLayout: 'cluster#balloonCarousel',
          // Устанавливаем собственный макет.
          clusterBalloonItemContentLayout: customItemContentLayout,
          // Устанавливаем режим открытия балуна.
          // В данном примере балун никогда не будет открываться в режиме панели.
          clusterBalloonPanelMaxMapArea: 0,
          // Устанавливаем размеры макета контента балуна (в пикселях).
          clusterBalloonContentLayoutWidth: 300,
          clusterBalloonContentLayoutHeight: 130,
          // Устанавливаем максимальное количество элементов в нижней панели на одной странице
          clusterBalloonPagerSize: 10,
          // Настройка внешнего вида нижней панели.
          // Режим marker рекомендуется использовать с небольшим количеством элементов.
          //clusterBalloonPagerType: 'marker',
          // Можно отключить зацикливание списка при навигации при помощи боковых стрелок.
          clusterBalloonCycling: false,
          // Можно отключить отображение меню навигации.
          //clusterBalloonPagerVisible: false
        });
        // Заполняем кластер геообъектами со случайными позициями.
        let placemarks = [];

        for (let i = 0; i < objects.length; i++) {
          let placemark = new window.ymaps.Placemark([objects[i].coordx, objects[i].coordy], {
            // Устаналиваем данные, которые будут отображаться в балуне.
            balloonContentHeader: getContentHeadder(objects[i]),
            balloonContentBody: getContentBody(objects[i]),
            balloonContentFooter: getContentFooter(objects[i]),
            placemarkId: i,
          }, {
            balloonContentLayout: soloCustomContentLayout,
            iconImageClipRect: [[1000, 0], [1, 5]],
            iconImageSize: [1, 10],
            iconImageOffset: [-1, -12],
          });
          placemarks.push(placemark);
        }
        clusterer.add(placemarks);

        //События для смены цвета меток при наведении на них и при нажатии
        clusterer.events
            // Можно слушать сразу несколько событий, указывая их имена в массиве.
            .add(['mouseenter', 'mouseleave', 'click'], function (e) {
              let target = e.get('target'),
                  type = e.get('type');
              if (typeof target.getGeoObjects != 'undefined') {
                // Событие произошло на кластере.
                if (type === 'mouseenter') {
                  target.options.set('preset', 'islands#greenClusterIcons');
                } else {
                  target.options.unset('preset');
                }
              } else {
                // Событие произошло на геообъекте.
                if (type === 'mouseenter') {
                  target.options.set('preset', 'islands#greenIcon');
                } else {
                  target.options.unset('preset');
                }
              }
            });

        map.geoObjects.add(clusterer);
        return placemarks;
      }

      /**
       * Оформление тела карточки
       * @param obj
       * @returns {string}
       */
      function getContentBody(obj) {
        return '<b class="ballon_body">' + obj.sername+ ' '+ obj.name + ' '+ obj.secondname + '</b>';
      }

      /**
       * Оформление заголовка карточки
       * @param obj
       * @returns {string}
       */
      function getContentHeadder(obj) {
        return '<img class ="image image-border" src = "/graduate_photos/' + obj.img + '" alt = "Фото выпускника">';
      }

      /**
       * Оформление футера карточки
       * @param obj
       * @returns {string}
       */
      function getContentFooter(obj) {
        return '<div class = "ballon_footer-city">' + obj.workcity + '</div><div class ="ballon_footer-job">' + obj.job + '</div>'
      }
    },

    /**
     * Включает и отключает тепловую карту выпускников
     */
    enableHeatMap() {
      /*Меняем положение с вкл на вкл или на оборот*/
      this.heatMapActive = !this.heatMapActive;
      let data = [];
      if (!this.heatMapActive) {
        this.showPlacemarks();
        //Выключаем гео-объекты
        for (let i = 0; i < this.objects.length; i++) {
          data.push([this.objects[i].coordx, this.objects[i].coordy]);
        }
      } else this.hidePlacemarks();
      this.heatMap.setData(data);
    }
  },
  /**
   * Выполняется при создании страницы
   */
  created() {
    //при создании страницы
    let then = this;

    //Устанавливаем настройки для карты
    const mapSettings = {
      /*Наш API ключ*/
      apiKey: '45ec4162-27ee-45ca-bc97-12756e85ccaf',
      lang: 'ru_RU',
      coordorder: 'latlong',
      enterprise: false,
      version: '2.1'
    };
    //Применякм настройки карты
    loadYmap({mapSettings, debug: true});
    //Создаем слои карты и тепловой карты
    window.ymaps.ready(['Heatmap']).then(function init() {
      // Создание карты.
      let map = new window.ymaps.Map('map', {
        center: then.mapCenterCoords,
        zoom: then.mapZoom,
        controls: then.mapControls,
      });
      //Устанавливаем метки на карту
      then.placemarks = then.setPlaseMarks(map, then.objects);

      //Устанавливаем обработчик для клика на баллун
      jQuery(document).on("click", "button.list_item", function () {
        // Определяем по какой метке произошло событие.
        then.chosenGraduateId = jQuery(this).data().placemarkid;
        then.showCard = true;
      });
      //Настраиваем тепловую карту БЕЗ МЕТОК
      let heatmap = new window.ymaps.Heatmap([], {
        // Радиус влияния.
        radius: 15,
        // Нужно ли уменьшать пиксельный размер точек при уменьшении зума. False - не нужно.
        dissipating: false,
        // Прозрачность тепловой карты.
        opacity: 0.6,
        // Прозрачность у медианной по весу точки.
        intensityOfMidpoint: 0.1,
        // JSON описание градиента.
        gradient: {
          0.1: 'rgb(230,255,0)',
          0.4: 'rgb(102,255,0)',
          0.6: 'rgb(0,255,128)',
          0.8: 'rgb(0,225,255)',
          1.0: 'rgb(10,23,232)'
        }
      });
      //Сохраняем её в дату
      then.heatMap = heatmap;
      //Применяем тепловую карту на слой карты
      heatmap.setMap(map);
      then.map = map;
    });
  }
}

</script>
