<template>
  <v-card class="card">
    <v-card-title>
      <v-icon>{{icon}}</v-icon>
      {{title}}
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text>
      <v-form v-show="!send"
              v-model="valid">
        <v-text-field
            v-model="subject"
            :rules="subjectRules"
            label="Тема обращения"
            required
            outlined

        ></v-text-field>
        <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail, для ответа вам"
            required
            outlined
        ></v-text-field>
        <v-textarea
            v-model="massage"
            label="Текст обращения"
            :rules="massageRules"
            required
            full-width
            single-line
            outlined
            no-resize
        ></v-textarea>
        <br>
        <v-btn block large color="#1976D2" style="color: #FFFFFF" :disabled="!valid"
               @click="sendMassage()"> Отправить
        </v-btn>
      </v-form>
      <div v-show="send">
        <v-card-text style="text-align: center">
          <div v-if="sendStatus">
            <v-icon color="#56db40" large>mdi-check-circle-outline</v-icon>
            <span class="massge-text">Сообщение успешно отправлено!</span>
            <br>
            <br>
            <span>Эту страницу можно покинуть</span>
          </div>
          <div v-else>
            <v-icon color="#db4040" large>mdi-alert-circle-outline</v-icon>
            <span class="massge-text">Ошибка, сообщение не отправлено.</span>
            <br>
            <span class="sub-massage-text">Попробуйте позже.</span>
            <br>
            <br>
            <span>Эту страницу можно покинуть</span>
          </div>
        </v-card-text>
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
import axios from "axios";

export default {
  name: "FeedbackForm",
 props:{
    title: {
      default: '',
    },
   icon: {
     default: ''
   },
   type:{}
 },
  data: () => ({
    sendStatus: false,
    send: false,
    subject: '',
    subjectRules: [
      v => !!v || 'Введите тему обращения',
    ],
    email: '',
    emailRules: [
      v => !!v || 'Введите e-mail',
      v => /.+@.+\..+/.test(v) || 'Неверный формат e-mail',
    ],
    massage: '',
    massageRules: [
      v => !!v || 'Укажите описание вашей проблемы',
    ],
    valid: true,
  }),
  methods: {
    validate() {
      this.$refs.form.validate().then((result) => {
        if (result) {
          self.$refs.recaptcha.execute()
        }
      })
    },
    async sendMassage() {
      let res = false;
      await axios.post('http://localhost:3000/mail/send', {
        email: this.email,
        subject: this.subject,
        massage: this.massage,
        type: this.type,
      }).then(function (response) {
        res = response.data;
      }).catch(function ()
      {
        res = false;
      });
      this.send = true;
      this.sendStatus = res;
    },
  }
}
</script>

<style scoped>

.massge-text {
  font-size: 22px;
}
.sub-massage-text{
  font-size: 18px;
}
</style>